@import 'assets/scss/utils/mixins';

.agents-table-container {
	max-height: 600px;
	overflow: auto;
	width: 100%;

	&::-webkit-scrollbar {
		width: 0;
		height: 5px;
	}

	&::-webkit-scrollbar-thumb {
		background: var(--general-3);
		border-radius: 20px;
	}
}

.pending-status {
	background-color: var(--general-1);
	color: var(--general-4);
}

.accepted-status {
	background-color: var(--success-1);
	color: var(--success-3);
}

.agent-status {
	border-radius: 100px;
	width: 75px;
	height: 22px;
	line-height: 22px;
}

.remove-icon {
	width: 20px;
	height: 20px;
	font-size: 20px;
}

.w-200px {
	width: 200px;
}

findqo-agent-profile {
	width: 100%;

	@include media-breakpoint-up(md) {
		width: fit-content;
	}
}

@media (min-width: 768px) {
	.row-cols-md-3 > * {
		flex: 0 0 auto;
		width: 32.23%;
	}
}

.agent-count {
	height: fit-content !important;

	@include media-breakpoint-up(md) {
		height: 48px !important;
		display: inline-flex;
		align-items: center;
	}
}
